export default {
	data() {
		return {
			fkey: '',
			skey:''
		}
	},
	methods:{
		bindKey(e){
			this.fkey = e.detail.value
		},
		bindSkey(e){
			this.skey = e.detail.value
		},
		bindSubmit(){
			if(this.fkey == '' || this.skey == ''){
				uni.showToast({
					title: '请正确输入密码',
					icon:'none'
				})
			} else if(this.fkey < 6 || this.fkey > 7 || this.skey < 6 || this.skey > 7){
				uni.showToast({
					title: '密码长度不对，请重新输入',
					icon:'none'
				})
			} else if(this.fkey !== this.skey){
				uni.showToast({
					title:'两次密码输入不一致',
					icon:'none'
				})
			} else{
				uni.showToast({
					title:'设置成功'
				})
				setTimeout(()=>{
					uni.reLaunch({
						url:'../index/index'
					})
				},1000)
			}
		}
	}
}

